import React from 'react';
import { Modal,Input } from 'antd';

const Search = Input.Search;

class PopService1 extends React.Component {
  state = { visible: false }

  showModal = () => {
    this.setState({
      visible: true,
    });
  }

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  }
  onSearch = (value) => {
    console.log(value)
  }

  render() {
    return (
      <div>
        <a className="side-a" href="#" onClick={this.showModal}><img className="banner-img" src={require("./../../images/24hour2.png")}/></a>
        <Modal
          title="电话回拨"
          visible={this.state.visible}
          onCancel={this.handleCancel}
          className="pop"
          footer={null}
          width={370}
        >
            <Search
              placeholder="请输入您的联系电话"
              enterButton="电话回拨"
              onSearch={this.onSearch}
            />
            <p style={{color:'#999',fontSize:12,marginTop:10}}>输入接听手机号，点击号码回拨，等待电话客服为您致电！</p>
        </Modal>
      </div>
    );
  }
}
export default PopService1;

