import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Home from './pages/Home.js'
import LiveEntertainment from './pages/LiveEntertainment.js'
import Esports from './pages/Esports.js'
import Account from './pages/Account.js'
import Chess from './pages/Chess.js'
import Sports from './pages/Sports.js'
import Lottery from './pages/Lottery.js'
import Activity from './pages/Activity.js'
import Play from './pages/Play.js'
import Join from './pages/Join.js'
import ServiceCenter from './pages/ServiceCenter.js'
import VIP from './pages/VIP.js'

import Header from './components/Header.js'
import Footer from './components/Footer.js'
import Register from './pages/Register.js'
import Brand from './pages/Brand.js'


class App extends Component {
  render() {
      return (
        <Router>
            <div className ="App">
                <Header />
                    <Route exact path="/" component={Home}/>
                    <Route path="/LiveEntertainment" component={LiveEntertainment}/>
                    <Route path="/Esports" component={Esports}/>
                    <Route path="/Chess" component={Chess}/>
                    <Route path="/Sports" component={Sports}/>
                    <Route path="/Lottery" component={Lottery}/>
                    <Route path="/Activity" component={Activity}/>
                    <Route path="/Join" component={Join}/>
                    <Route path="/Play" component={Play}/>
                    <Route path="/ServiceCenter" component={ServiceCenter}/>
                    <Route path="/register" component={Register}/>
                    <Route path="/account" component={Account} />
                    <Route path="/VIP" component={VIP} />
                    <Route path="/Brand" component={Brand} />
                <Footer />
            </div>
        </Router>

      );
  }
}

export default App;
