import React, { Component } from 'react';
import {Row ,Col,Card ,Icon } from 'antd';
import { PopBind } from './sub/PopBind.js'
import { PopBindNew } from './sub/PopBindNew.js'
function MyCard (props){
    return (
        <Row gutter={16} type="flex" justify="space-around">
        {
            props.todos.map( (todo,i) =>
            <Col span={8}> 
                <Card
                    title={todo.title}
                    extra={<PopBind />}
                    bordered={false}
                    style={{ background :todo.color }}
                    key={ todo.id }
                    index={ i }
                    className="card-bank"
                    >
                    <p className="number">{todo.number}</p>
                    <p>{todo.name}</p>
                </Card>
            </Col>
            )
        }
            <Col span={8}>
                <PopBindNew />
            </Col>
        </Row>
    )
}

class LiveEntertainment extends Component {
    constructor(props){
        super(props);
        this.state = {
            todos:[{id:1,title:'中国银行',number:'1234123412341234',name:'周长生',color:'#b91f27'},
            {id:2,title:'工商银行',number:'1234123412341234',name:'周明',color:'#006ab0'}]
        }
    }
    render (){
        return (
            <div className="App-main">
                <Row style={{borderLeft:'3px solid #ffc742',background:'#1f2429'}} >
                    <Col span={8} style={{fontSize:'18px',padding:'15px 20px'}}>银行卡绑定</Col>
                </Row>
                    <MyCard todos={this.state.todos}/>
                    
            </div>
        )
    }
}
export default LiveEntertainment

