import React from 'react';
import { 
    Row, Col ,Button ,Modal,Form,Input ,Select
} from 'antd';

function hasErrors(fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
} 
class FindForm extends React.Component {
  componentDidMount() {
    // To disabled submit button at the beginning.
    this.props.form.validateFields();
  }
    state = {

      confirmDirty: false,
      autoCompleteResult: [],
    };
    error() {
        Modal.error({
          title: '请输入正确的手机号',
          content: '',
        });
    }
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFields((err, values) => {
        if (!err) {
            console.log('Received values of form: ', values);
        }else{
          this.error();
        }
      });
      // this.props.form.validateFieldsAndScroll((err, values) => {
      //   if (!err) {
      //     console.log('Received values of form: ', values);
      //   }else{
      //       this.error()
      //   }
      // });
    }
  
    handleConfirmBlur = (e) => {
      const value = e.target.value;
      this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }
    render() {
      const { getFieldDecorator ,getFieldsError,getFieldError, isFieldTouched,} = this.props.form;
      const phoneError = isFieldTouched('phone') && getFieldError('phone');
      const captchaError = isFieldTouched('captcha') && getFieldError('captcha');

      const formItemLayout = {
        colon: false,
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
      const { Option } = Select;
      const prefixSelector = getFieldDecorator('prefix', {
        initialValue: '86',
      })(
        <Select style={{ display: 'none' }}>
          <Option value="86">+86</Option>
          <Option value="87">+87</Option>
        </Select>
      );
    return (
        <Form onSubmit={this.handleSubmit} className="find-form"> 
            <Form.Item
                validateStatus={phoneError ? 'error' : ''}
                help={phoneError || ''}
                {...formItemLayout}
                label="绑定手机"
            >
                {getFieldDecorator('phone', {
                rules: [{ required: true, message: '手机号码格式不正确' }],
                })(
                <Input placeholder="请输入绑定账号的手机号" style={{ width: '100%' }} />
                )}
            </Form.Item>
            <Form.Item
                validateStatus={captchaError ? 'error' : ''}
                help={captchaError || ''}
                {...formItemLayout}
                label="短信验证"
                //extra="We must make sure that your are a human."
            >
                <Row gutter={4}>
                <Col span={8}>
                    {getFieldDecorator('captcha', {
                    rules: [{ required: true, message: ' 验证码不正确 ' }],
                    })(
                    <Input />
                    )}
                </Col>
                <Col span={16}>
                    <Button  type="primary" style={{color:'#000'}}>获取</Button>
                    <a href="#" style={{marginLeft:'2px'}}> 收不到？</a>
                </Col>
                </Row>
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>
                <Button 
                  type="primary" 
                  htmlType="submit" 
                  disabled={hasErrors(getFieldsError())}
                  >确定</Button>
            </Form.Item>
        </Form>
    );
    }
  }
  
export const WrappedFindForm = Form.create({ name: 'register' })(FindForm);
