import React from 'react';
import {
    Form, Row,Col,  Button, Tag, Select,InputNumber,
} from 'antd';
  
const { Option } = Select;

class PreAllTypeForm extends React.Component {

    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }

    render() {
      const { getFieldDecorator } = this.props.form;  
      const formItemLayout = {
        labelCol: {
          xs: { span: 16 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form onSubmit={this.handleSubmit}>        
          <Form.Item
            {...formItemLayout}
            label="游戏类型"
          >
            {getFieldDecorator('gameName', {
              rules: [{ required: true, message: '请选择类型'}],
            })(
                <Select >
                    <Option value="1">真人</Option>
                    <Option value="2">老虎机</Option>
                </Select>
            )}
          </Form.Item>
         <Row type="flex" justify="start" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={4} offset={4} className="red-bg">温馨<br />提示</Col>
                <Col span={15} offset={1} >1、自助返水最低金额为1元。（要是您没达到自助返水的最低金额是不能自助返水的）。<br />
2、自助返水无次数限制，自助返水申请提交后，会在5分钟内通过审核，返水金额会自动添加到您的本地钱包。<br />
3、自助返水有一定延时性,与实际投注相差半小时。
                </Col>
            </Row>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">确定</Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
 export const PreAllType2 = Form.create({ name: 'register' })(PreAllTypeForm);
