import React from 'react';
import {
  Button, Modal, Form, Input, Cascader, Row,Col,Select
} from 'antd';


const { Option } = Select;
const residences = [{
  value: 'zhejiang',
  label: 'Zhejiang',
  children: [{
    value: 'hangzhou',
    label: 'Hangzhou',
    children: [{
      value: 'xihu',
      label: 'West Lake',
    }],
  }],
}, {
  value: 'jiangsu',
  label: 'Jiangsu',
  children: [{
    value: 'nanjing',
    label: 'Nanjing',
    children: [{
      value: 'zhonghuamen',
      label: 'Zhong Hua Men',
    }],
  }],
}];
const CollectionCreateForm = Form.create({ name: 'form_in_modal' })(
  // eslint-disable-next-line
  class extends React.Component {
    
    render() {
      const {
        visible, onCancel, onCreate, form,
      } = this.props;
      const { getFieldDecorator } = form;
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      return (
        <Modal
          visible={visible}
          title="绑定银行卡"
          okText="提交"
          cancelText="取消"
          onCancel={onCancel}
          onOk={onCreate}
          className="pop"
          width={520}
        >
          <Form >
            <Form.Item l
            {...formItemLayout}
            label="账户姓名"
            >
              {getFieldDecorator('name', {
                rules: [{ required: true, message: ' 姓名不能为空!' }],
              })(
                <Input />
              )}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label="银行账号"
            >
              {getFieldDecorator('card-number',{
                rules: [{ required: true, message: ' 银行账号不能为空!' }],
              })(<Input />)}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label="银行名称"
            >
              {getFieldDecorator('bank-type')(
                <Select placeholder="请选择银行名称">
                  <Option value="china">中国银行</Option>
                  <Option value="jh">建设银行</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label="开户省份"
            >
              {getFieldDecorator('residence', {
                initialValue: ['zhejiang', 'hangzhou', 'xihu'],
                rules: [{ type: 'array', required: true, message: 'Please select your habitual residence!' }],
              })(
                <Cascader options={residences} className="residences"/>
              )}
            </Form.Item>
            <Form.Item 
            {...formItemLayout}
            label="开户支行"
            >
              {getFieldDecorator('card-number',{
                rules: [{ required: true, message: ' 开户支行不能为空!' }],
              })(<Input placeholder="请输入开户支行"/>)}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label="短信验证"
                //extra="We must make sure that your are a human."
            >
                <Row gutter={8}>
                <Col span={12}>
                    {getFieldDecorator('captcha', {
                    rules: [{ required: true, message: ' 验证码不正确 ' }],
                    })(
                    <Input />
                    )}
                </Col>
                <Col span={12}>
                    <Button  type="primary" style={{color:'#000'}}>获取</Button>
                    <a href="#" style={{marginLeft:'5px'}}> 收不到？</a>
                </Col>
                </Row>
            </Form.Item>
          </Form>
        </Modal>
      );
    }
  }
);

export class PopBindNew extends React.Component {

  state = {
    visible: false,
  };

  showModal = () => {
    this.setState({ visible: true });
  }

  handleCancel = () => {
    this.setState({ visible: false });
  }

  handleCreate = () => {
    const form = this.formRef.props.form;
    form.validateFields((err, values) => {
      if (err) {
        return;
      }

      console.log('Received values of form: ', values);
      form.resetFields();
      this.setState({ visible: false });
    });
  }

  saveFormRef = (formRef) => {
    this.formRef = formRef;
  }

  render() {
    return (
      <div>
        <div className="add-bank"  onClick={this.showModal}>+</div>
        <CollectionCreateForm
          wrappedComponentRef={this.saveFormRef}
          visible={this.state.visible}
          onCancel={this.handleCancel}
          onCreate={this.handleCreate}
        />
      </div>
    );
  }
}