import React from 'react';
import { Link } from 'react-router-dom';

import { 
    Row, Col ,Button, Icon ,Modal,Form,Input,Checkbox
} from 'antd';
import { WrappedFindForm } from './FindPassword.js';

function hasErrors(fieldsError) {
    return Object.keys(fieldsError).some(field => fieldsError[field]);
}

class NormalLoginForm extends React.Component {
    componentDidMount() {
        // To disabled submit button at the beginning.
        this.props.form.validateFields();
      }
    state = {
        ModalText: 'Content of the modal',
        visible: false,
        confirmLoading: false,
      }
    
    showModal = () => {
        this.setState({
          visible: true,
        });
    }
    handleOk = () => {
        this.setState({
          ModalText: 'The modal will be closed after two seconds',
        });
        setTimeout(() => {
          this.setState({
            visible: false,
            confirmLoading: false,
          });
        }, 2000);
    }
    handleCancel = () => {
        this.setState({
          visible: false,
        });
    }
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFields((err, values) => {
        if (!err) {
            console.log('Received values of form: ', values);
            this.props.handleLogin(values.userName)
        }
      });
    }
  
    render() {
      const { getFieldDecorator ,getFieldsError,getFieldError, isFieldTouched,} = this.props.form;
      const { visible, confirmLoading } = this.state;
      const userNameError = isFieldTouched('userName') && getFieldError('userName');
      const passwordError = isFieldTouched('password') && getFieldError('password');
      return (
        <Form onSubmit={this.handleSubmit} className="login-form">
                <Modal
                    title="找回账号"
                    visible={visible}
                    onOk={this.handleOk}
                    confirmLoading={confirmLoading}
                    onCancel={this.handleCancel}
                    width={360}
                    footer={null}
                    className="pop"
                    destroyOnClose
                    >
                    <WrappedFindForm />
                </Modal>
            <Form.Item
                validateStatus={userNameError ? 'error' : ''}
                help={userNameError || ''}
            >
                {getFieldDecorator('userName', {
                    rules: [{ required: true, message: '请输入账号!' }],
                })(
                    <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Username" />
                )}
            </Form.Item>
            <Form.Item
                validateStatus={passwordError ? 'error' : ''}
                help={passwordError || ''}
            >
                {getFieldDecorator('password', {
                rules: [{ required: true, message: '请输入密码!' }],
                })(
                <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
                )}
            </Form.Item>
            <Form.Item>
                {getFieldDecorator('remember', {
                    valuePropName: 'checked',
                    initialValue: false,
                })(
                    <Checkbox className="checkbox"><a href="#">记住账号</a></Checkbox>
                )}
                
                <Button 
                    type="primary" 
                    htmlType="submit" 
                    className="login-form-button"
                    disabled={hasErrors(getFieldsError())}
                >立即登录</Button>
                <Row type="flex" justify="space-between" style={{'textAlign':'center'}}>
                    <Col span={8} >
                        <Link to='/register' onClick={this.props.isVisible} >免费注册</Link></Col>
                    <Col span={8}><a href="#" onClick={this.showModal} >找回账号</a></Col>
                    <Col span={8}><a  href="#" >忘记密码</a></Col>
                </Row>
            </Form.Item>
        </Form>
      );
    }
}

export const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(NormalLoginForm);

