import React from 'react';
import {
    Modal,Form, Input, Select, Row, Col, Checkbox, Button, AutoComplete,
} from 'antd';

const { Option } = Select;
const AutoCompleteOption = AutoComplete.Option;

class RegistrationForm extends React.Component {
    state = {
      confirmDirty: false,
      autoCompleteResult: [],
    };
    error() {
        Modal.error({
          title: 'This is an error message',
          content: 'some messages...some messages...',
        });
      }
  
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        
        if (!err) {
          console.log('Received values of form: ', values);
        }else{
            this.error()
        }
      });
    }
  
    handleConfirmBlur = (e) => {
      const value = e.target.value;
      this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }
  
    compareToFirstPassword = (rule, value, callback) => {
      const form = this.props.form;
      if (value && value !== form.getFieldValue('password')) {
        callback('Two passwords that you enter is inconsistent!');
      } else {
        callback();
      }
    }
  
    validateToNextPassword = (rule, value, callback) => {
      const form = this.props.form;
      if (value && this.state.confirmDirty) {
        form.validateFields(['confirm'], { force: true });
      }
      callback();
    }
  
    handleWebsiteChange = (value) => {
      let autoCompleteResult;
      if (!value) {
        autoCompleteResult = [];
      } else {
        autoCompleteResult = ['.com', '.org', '.net'].map(domain => `${value}${domain}`);
      }
      this.setState({ autoCompleteResult });
    }
  
    render() {
    const { getFieldDecorator } = this.props.form;
    const { autoCompleteResult } = this.state;
  
    const formItemLayout = {
        colon: false,
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
    const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
  
    const websiteOptions = autoCompleteResult.map(website => (
        <AutoCompleteOption key={website}>{website}</AutoCompleteOption>
      ));
    const { Option } = Select;
    const prefixSelector = getFieldDecorator('prefix', {
        initialValue: '86',
      })(
        <Select style={{ display: 'none' }}>
          <Option value="86">+86</Option>
          <Option value="87">+87</Option>
        </Select>
      );
    return (
    <Row type="flex" justify="center" id="register-page" className="pageWidth">
        <Col span={19}><h1>欢迎注册</h1>
            <Form onSubmit={this.handleSubmit} className="register-page">
                <Form.Item
                    {...formItemLayout}
                    label={(
                    <span>
                        游戏账户
                    </span>
                    )}
                >
                    {getFieldDecorator('nickname', {
                    rules: [{ 
                        required: true, 
                        message: '用户名不能是纯数字，且长度为4-10个字符！', 
                        pattern:'/[a-zA-Z0-9_-]{4,10}/',
                        whitespace: true }],
                    },{
                        validator: this.validateNickName,
                    })(
                    <Input placeholder="用户名支持字母、数字、“_”、“-”组合，4-10个字符"/>
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}

                    label="密码登录"
                >
                    {getFieldDecorator('password', {
                    rules: [{
                        required: true, message: '密码为8-12位数字或英文字母，英文开头',
                        pattern:'/^[a-zA-Z0-9]{8,12}$/',
                    }, {
                        validator: this.validateToNextPassword,
                    }],
                    })(
                    <Input type="password" placeholder="密码为8-12位数字或英文字母，英文开头"/>
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label={(
                        <span>真实姓名</span>
                    )}
                >
                    {getFieldDecorator('realname', {
                    rules: [{ required: true, message: '必须与提款卡号姓名一致，否则不能提款', whitespace: true }],
                    })(
                    <Input placeholder="必须与提款卡号姓名一致，否则不能提款"/>
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="手机号码"
                >
                    {getFieldDecorator('phone', {
                    rules: [{ required: true, type: 'number', message: '手机号码格式不正确' }],
                    })(
                    <Input placeholder="请输入有效的电话号码，关系您的账户安全" style={{ width: '100%' }} />
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="短信验证"
                    //extra="We must make sure that your are a human."
                >
                    <Row gutter={8}>
                    <Col span={12}>
                        {getFieldDecorator('captcha', {
                        rules: [{ required: true, message: ' 验证码不正确 ' }],
                        })(
                        <Input />
                        )}
                    </Col>
                    <Col span={12}>
                        <Button  type="primary" style={{color:'#000'}}>获取</Button>
                        <a href="#" style={{marginLeft:'5px'}}> 收不到？重新获取</a>
                    </Col>
                    </Row>
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="电子邮箱"
                >
                    {getFieldDecorator('email', {
                    rules: [{
                        type: 'email', 
                        message: '请输入有效的电子邮箱',
                        pattern: '/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/',
                    }, {
                        required: false, message: 'Please input your E-mail!',
                    }],
                    })(
                    <Input placeholder="请输入有效的电子邮箱"/>
                    )}
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="推广代码"
                >
                    {getFieldDecorator('website', {
                    rules: [{ required: false, message: '' }],
                    })(
                    <AutoComplete
                        dataSource={websiteOptions}
                        onChange={this.handleWebsiteChange}
                        placeholder="请输入推广代码"
                    >
                        <Input />
                    </AutoComplete>
                    )}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    {getFieldDecorator('agreement', {
                    valuePropName: 'checked',
                    })(
                    <Checkbox>我已阅读并同意接受所有 《规则》与《条款》</Checkbox>
                    )}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit" style={{color:'#000',padding:'0 100px'}}>立即注册</Button>
                </Form.Item>
            </Form>
        </Col>
    </Row>
    );
    }
  }
  
const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
export default WrappedRegistrationForm
