import React, { Component } from 'react';
import {   Row ,Col,Form,Radio,Input ,Button,Tag} from 'antd';

class onlienForm extends React.Component {
    state = {
        confirmDirty: false,
        value: '',
        getMoneyMethod: '',
    };

    handleSubmit = (e) => {
      e.preventDefault();

      this.props.form.setFieldsValue({
        zhifuMethod : '1',
      });
      
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
          //this.props.handleLink(values.getMoneyMethod)
            this.setState({
                getMoneyMethod:values.getMoneyMethod,
            });
        }
      });
    }
    onChange = (e) => {
        console.log('radio checked', e.target.value);
        this.setState({
            value: e.target.value,
        });
    }
    render (){
        const { getFieldDecorator } = this.props.form;
        const style = {
            width:100,
            display:'inline-block',
            position:'absolute',
            top: '65px',
            left: '44px',
        } 

        const formItemLayout = {
            colon: false,
            labelCol: { span: 1 },
            wrapperCol: { span: 17 },
          }
        const tailFormItemLayout = {
            wrapperCol: {
              xs: {
                span: 23,
                offset: 4,
              },
              sm: {
                span: 17,
                offset: 4,
              },
            },
          };
          
        let page ;
             if(this.state.getMoneyMethod){
            page = (
                <div>
                    <p className="red-font">收款账号或二维码，仅本次有效。下次存款请重新获取</p>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0",background:"#141414"}}>
                        <Col span={20} offset={2}>请确认收款信息，并完成存款</Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0",background:"#1f2125"}}>
                        <Col span={4} offset={3} className="gray-font">存款方式：</Col>
                        <Col span={12}>微信扫码支付</Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0"}}>
                        <Col span={4} offset={3} className="gray-font">存款账户：</Col>
                        <Col span={12}>kaixin2018</Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0",background:"#1f2125"}}>
                        <Col span={4} offset={3} className="gray-font">存款金额：</Col>
                        <Col span={12}><span className="red-font">1000</span>&nbsp;<span style={{fontSize:'10px'}}>请支付此金额</span></Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle" style={{padding:"10px 0"}}>
                        <Col span={4} offset={3} className="gray-font">实际到账：</Col>
                        <Col span={12}>998</Col>
                    </Row>
                    <Row type="flex" justify="space-around" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                        <Col span={2} offset={1} className="red-bg">温馨<br />提示</Col>
                        <Col span={21}>系统到账时间将以银行入账时间为准，如超过<span className="red-font">10分钟</span>还未到账，请联系<a href="#">在线客服</a>，
<br />并提供该交易记录详细截图。</Col>
                    </Row>
                    <Row type="flex" justify="start" align="middle">
                        <Col span={8}><Button type="primary">去扫码二维码付款</Button></Col>
                        <Col span={4}><a>取消返回</a></Col>
                    </Row>
                    
                </div>            
            )
        }else{
            page = (
            <Form onSubmit={this.handleSubmit}>
                <Form.Item
                    {...formItemLayout}
                    label="存款方式"
                >
                    {getFieldDecorator('zhifuMethod')(
                        <Tag color="gold" style={{textAlign:'center',height:60,padding:'10px'}}>微信扫码支付<span style={{display:'block',fontSize:'0.03em'}}>便捷，适合小额存款</span></Tag>
                    )}
                    <p className="note-wrap" >支付宝转账返赠1%。<br/>
    请登录您的支付宝，点击转账到银行卡功能进行转账支付。支付宝中到账成功后查看游戏账户余额。<br/>
    存款皆以官网为准，且收款信息是实时更新，每次存款请重新下单获取。</p>
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="存款通道"
                >
                    {getFieldDecorator('tongdaoMethod')(
                        <Radio.Group initialValue="1" buttonStyle="solid">
                            <Radio.Button value="1" style={{marginRight:8}}>通道1</Radio.Button>
                            <Radio.Button value="2" style={{marginRight:8}}>通道2</Radio.Button>
                        </Radio.Group>
                    )}
                    <br /><p className="gray-font">系统会在存款金额后随机添加小数点，方便快速到账</p>
                </Form.Item>
                <Form.Item
                    {...formItemLayout}
                    label="存款金额"
                >
                    {getFieldDecorator('getMoneyMethod')(
                        <Radio.Group onChange={this.onChange} value={this.state.value}  buttonStyle="solid">
                            <Radio.Button value={50} style={{marginRight:8}}>50</Radio.Button>
                            <Radio.Button value={100} style={{marginRight:8}} checked={true}>100</Radio.Button>
                            <Radio.Button value={200} style={{marginRight:8}}>200</Radio.Button>
                            <Radio.Button value={500} style={{marginRight:8}}>500</Radio.Button>
                            <Radio.Button value={1000} style={{marginRight:8}}>1000</Radio.Button>
                            <Radio.Button value={2000} style={{marginRight:8}}>2000</Radio.Button>
                            <Radio.Button value={3000} style={{marginRight:8}}>3000</Radio.Button><br />
                            <Radio  value={50000}>
                                其他金额{this.state.value === 50000 ? <Input placeholder="11万-5万" style={{ width: 100, marginLeft: 10 }} /> : null}
                            </Radio>
                            <br />您将获得 <span className="red-font">100</span> 商城积分，可用于商城礼品兑换。
                        </Radio.Group>
                    )}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit" style={{padding:'0 50px',marginTop:30}}>获取收款账号</Button>
                </Form.Item>
            </Form>
            )
        }
        return (
            
            <div className="recharge">
                {page}
            </div>
        )
    }
}

 const online = Form.create({ name: 'weixin' })(onlienForm);

 export default online
