import React, { Component } from 'react';
import {  Table ,Row,Col ,Icon,Modal} from 'antd';

const columns = [{
    title: '',
    dataIndex: 'name',
    key: 'name',
    render: (text) => <a href="javascript:;"><Icon type="mail" /> {text}</a>,
  }, {
    title: '',
    dataIndex: 'text',
    key: 'text',
  }, {
    title: '',
    dataIndex: 'time',
    key: 'time',
  }];

const data = [{
    key: '1',
    name: '系统通知',
    text: '系统通知为您介绍1月优惠',
    time: '2019-01-02 10:19:24',
  }, {
    key: '2',
    name: '系统通知',
    text: '系统通知为您介绍1月优惠',
    time: '2019-01-02 10:19:24',
    tags: ['loser'],
  }, {
    key: '3',
    name: '系统通知',
    text: '系统通知为您介绍1月优惠',
    time: '2019-01-02 10:19:24',
  }];

class Mail extends Component {
    state = { 
        visible: false ,
        text: '',
        time: '',
        name: ''
    }
    
    showModal = () => {
        this.setState({
            visible: true,
        });
    }
    onCancel = () => {
        this.setState({
            visible: false,
        });
    }
    handleMail = (record)=> {
        console.log(record)
        this.setState({
            visible: true,
            text: record.text,
            time: record.time,
            name: record.name,
        });
    }
    
    render (){
        return (
            <div>
                <Modal
                    title={this.state.name}
                    visible={this.state.visible}
                    footer={null}
                    className="pop"
                    onCancel={this.onCancel}
                    >
                    <div>
                        <a href="#">转账即赠，延迟再赔。</a>
                        <br /><br />时间： {this.state.time}
                        <br /><br />亲爱的会员：
                        <br /><br />{this.state.text}
                    </div>
                    
                    </Modal>
                <Row style={{borderLeft:'3px solid #ffc742',background:'#1f2429'}} >
                    <Col span={8} style={{fontSize:'18px',padding:'15px 20px'}}>站内信</Col>
                </Row>
                <Table 
                    onRow={(record) => {
                        return {
                          onClick: (event) => { return this.handleMail(record)},       // 点击行
                          onDoubleClick: (event) => {},
                          onContextMenu: (event) => {},
                          onMouseEnter: (event) => {},  // 鼠标移入行
                          onMouseLeave: (event) => {}
                        };
                      }}
                    columns={columns} 
                    dataSource={data} 
                    pagination={{ pageSize: 10 }}
                    locale={{ emptyText: '暂无数据' }}
                />
                </div>
        )
    }
}
export default Mail