import React from 'react';
import {
    Form, Row,Col,  Button, InputNumber,
} from 'antd';
 
  class PreAllTypeForm extends React.Component {
  
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }

    render() {
      const { getFieldDecorator } = this.props.form;  
      const formItemLayout = {
        labelCol: {
          xs: { span: 16 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label="优惠代码"
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '不能为空!' }],
            })(
              <InputNumber 
                defaultValue={20}
              />
            )}
           </Form.Item>
           <Form.Item
            {...formItemLayout}
            label="存款金额"
          >
            {getFieldDecorator('priceMin', {
              rules: [{ required: true, message: '存款金额不能为空!' }],
            })(
              <InputNumber 
                defaultValue={10}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
         <Row type="flex" justify="start" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={4} offset={4} className="red-bg">温馨<br />提示</Col>
                <Col span={15} offset={1} >1、红包优惠券须达到限定投注额或是账户小于五元游戏账户低于5元才能使用红包优惠券，
     填写红利代码，然后去户内转账带入游戏。<br />

2、使用红包优惠券无需进行存款，优惠券有相应的流水倍数，才可进行户内转账。<br />

3、提交后可到账户清单，优惠记录裡查询相关使用记录。<br />

4、请确认存送优惠相关规则条款，如：玩家在平台不支持使用存送优惠进行投注，
     欧博将取消存送优惠并同时扣除通过存送优惠所带来的投注盈利。<br />
<br />


                </Col>
            </Row>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">确定</Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
 export const PreAllType4 = Form.create({ name: 'register' })(PreAllTypeForm);
