import React from 'react';
import {
    Form, Row,Col,  Button, Tag, Select,InputNumber,
} from 'antd';
  
const { CheckableTag } = Tag;
const { Option } = Select;

class MyTag extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            checked: true
        };
      }

  handleChange = (checked) => {
    this.setState({ checked });
  }

  render() {
    return <CheckableTag {...this.props} checked={this.state.checked} onChange={this.handleChange} />;
  }
}
function MyTagGroup (props){
    return (
        <div>
            { 
                props.todos.map( (todo,i) => 
                    <MyTag index={i} key={todo.id} text={todo.text}>{todo.text}</MyTag>
                )
            }
        </div>
    )
}
  
  class PreAllTypeForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = { todos: [{ id: 1, text: '全部类型'}, { id: 2, text: '老虎机'}, { id: 3, text: '真人'}, { id: 4, text: '电子棋牌'}] };
    }
  
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }

    render() {
      const { getFieldDecorator } = this.props.form;  
      const formItemLayout = {
        labelCol: {
          xs: { span: 16 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form onSubmit={this.handleSubmit}>
          <Form.Item
            {...formItemLayout}
            label="游戏类型"
          >
            {getFieldDecorator('gameType', {
              rules: [{
                validator: this.checkType
              }],
            })(
              <MyTagGroup todos={this.state.todos} />
            )}
          </Form.Item>
        
          <Form.Item
            {...formItemLayout}
            label="游戏类型"
          >
            {getFieldDecorator('gameName', {
              rules: [{ required: true, message: '请选择类型'}],
            })(
                <Select >
                    <Option value="1">真人30%首存优惠</Option>
                    <Option value="2">老虎机100%首存优惠</Option>
                    <Option value="3">老虎机笔笔存15%-30%</Option>
                </Select>
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="申请金额"
          >
            {getFieldDecorator('price', {
              rules: [{ required: true, message: '请输入申请金额!' }],
            })(
              <InputNumber 
                defaultValue={2}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="最低存款"
          >
            {getFieldDecorator('priceMin', {
              rules: [{ required: true, message: '请输入最低存款!' }],
            })(
              <InputNumber 
                defaultValue={10}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="红利金额"
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '请输入红利金额!' }],
            })(
              <InputNumber 
                defaultValue={0}
                formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                parser={value => value.replace(/\$\s?|(,*)/g, '')}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="流水倍数"
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '请输入流水倍数!' }],
            })(
              <InputNumber 
                defaultValue={20}
              />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="投注流水"
          >
            {getFieldDecorator('priceInterest', {
              rules: [{ required: true, message: '请输入投注流水!' }],
            })(
              <InputNumber 
                defaultValue={20}
              />
            )}
           </Form.Item>
         <Row type="flex" justify="start" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={4} offset={4} className="red-bg">温馨<br />提示</Col>
                <Col span={15} offset={1} >1、您可以以小于上笔存款的金额申请存款优惠<br />
2、申请优惠后,只可在同种游戏类型的平台互转,额度只能在达到投注流水后转出<br />
3、若完成流水倍数不能进行额度转出，请及时联系<span className="bule-font">在线客服</span>为您处理。<br />
                </Col>
            </Row>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">确定</Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
 export const PreAllType1 = Form.create({ name: 'register' })(PreAllTypeForm);
