import React from 'react';
import {
    Form, Input, Button, Row,Col
  } from 'antd';
  
  class ChangePhoneForm extends React.Component {
    state = {
      confirmDirty: false,
      visible: false 
    };
    showModal = () => {
        this.setState({
            visible: true,
        });
    }
    
    handleOk = (e) => {
        console.log(e);
        this.setState({
          visible: false,
        });
      }
    
    handleCancel = (e) => {
        console.log(e);
        this.setState({
          visible: false,
        });
      }
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }
  
    handleConfirmBlur = (e) => {
      const value = e.target.value;
      this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }

    render() {
      const { getFieldDecorator } = this.props.form;
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 18,
            offset: 10,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
     
      return (
        <Form onSubmit={this.handleSubmit}  className="phone-form" style={{margin:'25px 0 0'}}>
            <Form.Item
                {...formItemLayout}
                label="手机号码"
                style={{color:'#fff'}}
            >
                {getFieldDecorator('oldphone', {
                rules: [{ required: true, message: '请输入旧手机号' }],
                })(
                    <Input style={{ width: '100%' }} />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label="手机号码"
            >
                {getFieldDecorator('oldphone', {
                rules: [{ required: true, message: '请输入旧手机号' }],
                })(
                    <Input  style={{ width: '100%' }} />
                )}
            </Form.Item>
            <Form.Item
                {...formItemLayout}
                label="手机号码"
            >
                {getFieldDecorator('newphone', {
                rules: [{ required: true, message: '请输入新手机号' }],
                })(
                    <Input style={{ width: '100%' }} />
                )}
            </Form.Item>
            <Form.Item
            {...formItemLayout}
            label="Captcha"
            >
                <Row gutter={8}>
                    <Col span={12}>
                    {getFieldDecorator('captcha', {
                        rules: [{ required: true, message: '请输入验证码' }],
                    })(
                        <Input />
                    )}
                    </Col>
                    <Col span={6}>
                        <Button type="primary">获取</Button>
                    </Col>
                    <Col span={6}>
                        <a href="#">收不到？</a>
                    </Col>
                </Row>
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>
                <Button type="primary" htmlType="submit">提交</Button>
            </Form.Item>
        </Form>
        );
    }
  }
  
  export const WrappedChangePhoneFormForm = Form.create({ name: 'phoneForm' })(ChangePhoneForm);
  