import React from 'react';
import {
    Form, Input, Button,Row,Col,Select, Icon 
  } from 'antd';

  const Option = Select.Option;

  class withdrawForm extends React.Component {
    state = {
      confirmDirty: false,
    };
  
    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
        }
      });
    }
    
    checkPrice = ( value, callback) => {
      console.log(value)
        if (value >= 100) {
          callback();
          return;
        }
        callback('金额需大于100!');
      }
    render() {
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 2 },
          sm: { span: 1 },
        },
        wrapperCol: {
          xs: { span: 8 },
          sm: { span: 6 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };
    return (
        <Form onSubmit={this.handleSubmit} className="withdraw-form" style={{margin:"20px 0"}}>
          <Form.Item
            {...formItemLayout}
            label="账户金额"
          >
            {getFieldDecorator('totalPrice', {
              initialValue: 10000,
              rules: [{
                required: false, message: '',
              }],
            })(
              <Input disabled={true} className="red-font"/>
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="提款方式"
          >
            {getFieldDecorator('method', {
              rules: [{
                required: false, message: '',
              }],
            })(
                <input type="image" style={{width:'118px'}} alt="withdrawMethod"
                src={require('./../../images/r1.png')} />
            )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="提款银行"
          >
            {getFieldDecorator('bank', {
              rules: [{
                required: false, message: '',
              }],
            })(
                <Select
                  defaultValue="lucy"
                  dropdownRender={menu => (
                    <div>
                      {menu}
                      <div style={{ padding: '8px', cursor: 'pointer',background:'#000' }}>
                        <Icon type="plus" /> Add item
                      </div>
                    </div>
                  )}
                >
                  <Option value="">请选择银行</Option>
                  <Option value="1">浙江银行 3456</Option>
                  <Option value="2">浙江银行 3434</Option>
                </Select>
                )}
          </Form.Item>
          <Form.Item
            {...formItemLayout}
            label="提款金额"
          >
            {getFieldDecorator('Price', {
                initialValue: 100,
                rules: [{ validator: this.checkPrice }],
            })(
              <Input placeholder="金额>100" />
            )}
            <a href="#" className="all">全部</a>
          </Form.Item>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">提交</Button>
          </Form.Item>
            <Row type="flex" justify="space-around" align="middle" style={{fontSize:'10px',margin:'20px 0'}}>
                <Col span={2} offset={1} className="red-bg">温馨<br />提示</Col>
                <Col span={21}>1.如遇到提款问题或被取消，请联系在线客服为您查询。<br />
<br />2.提款姓名与卡号须与注册信息一致，否则无法提款</Col>
            </Row>
        </Form>
      );
    }
  }
 
  export const WrappedwithdrawForm = Form.create({ name: 'withdraw' })(withdrawForm);
  
