import React from 'react';
import { Modal } from 'antd';

export class PopBind extends React.Component {
  state = { visible: false }

  showModal = () => {
    this.setState({
      visible: true,
    });
  }

  handleOk = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }

  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }

  render() {
    return (
      <div>
        <a href="#" className="dele" onClick={this.showModal}>删除</a>
        <Modal
          title="温馨提示"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          className="pop"
          width={300}
          bodyStyle={{border:'0 none'}}
        >
          <p className="red-font">确认删除您的这张银行卡?</p>
        </Modal>
      </div>
    );
  }
}
